.sokomishalov {
  text-align: left;
  line-height: 1.5;
  font-size: 3vw;
  color: #22BAD9; }
  @media only screen and (max-width: 500px) {
    .sokomishalov {
      font-size: 6vw;
      padding: 10px 10px; } }
  @media only screen and (min-width: 500px) and (max-width: 1000px) {
    .sokomishalov {
      font-size: 4vw;
      padding: 30px 50px; } }
  @media only screen and (min-width: 1000px) {
    .sokomishalov {
      font-size: 2vw;
      padding: 50px 70px; } }
  .sokomishalov .name {
    color: #FF5166; }
  .sokomishalov a {
    color: #FF69B4; }
  .sokomishalov .kotlin {
    color: #F18E33; }
  .sokomishalov .java {
    color: #b07219; }
  .sokomishalov .js {
    color: #f1e05a; }
  .sokomishalov .dart {
    color: #00B4AB; }
  .sokomishalov .others {
    color: rgba(255, 255, 255, 0.5); }
  .sokomishalov .fan-of {
    margin-left: 10px; }
